.InspectionFrame {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(217, 217, 217);
}

.InspectionWrap {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.InspectionTitle {
    font-size: 2.2rem;
    font-weight: 600;
}

.warningImg {
    width: 200px;
}

.InspectionContext {
    font-size: 1rem;
}